import { createRouter, createWebHistory } from 'vue-router';
import V_AboutView from '@/views/V_AboutView.vue';
const routes = [
  {
    path:"/",
    name:"about",
    component:V_AboutView
  },
  {
    path:"/references",
    name:"references",
    component:()=> import("@/views/ReferencesCOM/V_ReferencesView.vue")
  },
  {
    path:"/images",
    name:"images",
    component:()=> import("@/views/ImagesCOM/V_ImagesView.vue")
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
